
import axios from 'axios';
import { defineComponent } from 'vue';
import baseUrl from '../config/baseUrl';
import Loading from '@/components/Loading.vue';
import TodoType from '../config/todoType';

export default defineComponent({
  name: 'TodoView',

  data: () => ({
    dataLoading: true,
    todo: {} as TodoType,
  }),

  components: {
    Loading,
  },
  
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      axios
        .get(`${baseUrl}/${this.$route.params.id}`)
        .then(({ data }) => {
          this.todo = data;
        })
        .catch((error) => {
          alert(
            `${error.response.status} (${error.response.statusText}), try again`
          );
        })
        .finally(() => {
          this.dataLoading = false;
        });
    },

    deleteTodo() {
      this.dataLoading = true;
      axios
        .delete(`${baseUrl}/${this.$route.params.id}`)
        .then(() => {
          this.$router.push('/');
        })
        .catch((error) => {
          alert(
            `${error.response.status} (${error.response.statusText}), try again`
          );
        })
        .finally(() => {
          this.dataLoading = false;
        });
    },
    toggleTodo(completed: boolean) {
      this.dataLoading = true;
      axios
        .put(`${baseUrl}/${this.$route.params.id}`, {
          done: !completed,
        })
        .then(() => {
          this.getData();
        })
        .catch((error) => {
          alert(
            `${error.response.status} (${error.response.statusText}), try again`
          );
        })
        .finally(() => {
          this.dataLoading = false;
        });
    },
  },
});
